:root {
  --blue-text: #00acdc;
}

body {
  background-color: #ccc;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__link {
  text-align: center;
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link.is-active {
  color: var(--blue-text);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue-text);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.nav__list:last-child {
  margin-right: 0;
}

.nav__link:hover {
  color: var(--blue-text);
  transition: color .15s;
}

.nav__link:active {
  color: var(--blue-text);
  font-weight: 500;
}

